.news-item{
    color: black;
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
    padding-top: 10px;
}

.news-item img{
    border-radius: 10px;
    margin-right: 30px;
    object-fit: cover;
    width: 140px;
    height: 120px;
}

.news-headline {
    font-size: 22px;
    margin-top: 0px;
    margin-bottom: 10px;
    font-weight: bold;
}

.news-content {
    display: flex;
    flex-direction: column !important;
    font: 14px Noto Sans JP;
}